import { useContext, useEffect, useState } from "react";
import { Products } from "./Context/Context";
import { MdDelete } from "react-icons/md";

const Cart = () => {
  const {
    state: { cart },
    dispatch
  } = useContext(Products);
  const [total, setTotal] = useState(0);
  const [qty, setQty] = useState(1);
  useEffect(() => {
    setTotal(
      cart.reduce((acc, curr) => acc + Number(curr.price) * curr.qty, 0)
    );
  }, [cart]);
  const increseQty = () => {
    setQty(qty + 1);
  };
  const decreseQty = () => {
    setQty(qty + 1);
  };
  const qtyUpdate = (item) => {
    dispatch({
      type: "CHANGE_CART_QTY",
      payload: {
        id: item.id,
        qty: qty
      }
    });
  };
  return (
    <div className="cart">
      <table>
        <thead>
          <tr>
            <th>Title</th>
            <th>Quantity</th>
            <th>Price</th>
          </tr>
        </thead>
        {cart.length
          ? cart.map((item) => {
              return (
                <tr>
                  <td>{item.title}</td>
                  <td className="qty">
                    <span
                      className="qty-button"
                      onClick={() => {
                        increseQty();
                        qtyUpdate(item);
                      }}
                    >
                      +
                    </span>
                    <span>{item.qty}</span>
                    <span className="qty-button">-</span>
                  </td>
                  <td>{Math.floor(item.price)}</td>
                  <td>
                    <MdDelete
                      onClick={() => {
                        dispatch({
                          type: "REMOVE FROM CART",
                          payload: item
                        });
                      }}
                    />
                  </td>
                </tr>
              );
            })
          : "Cart is empty"}
      </table>
      <div className="price">
        <span>Total Price</span>
        <h3>$ {Math.floor(total)}</h3>
      </div>
    </div>
  );
};

export default Cart;
